* {
  margin: 0;
  padding: 0;
}

img {
  width: 25px;
  height: 25px;
}

a {
  display: flex;
  justify-content: space-between;
  text-decoration: none;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #212121;
  margin: 0 auto;
  height: 70px;
  width: 100%;

}

.title {
  color: white;
}

.titleContainer{
  display: flex;
  align-items:center;
  justify-content: space-between;
  padding: 0 10px;
  width: 170px;
}

.navbar {
  display: flex;
  background-color: #212121;
  border: 2px solid white;
  border-radius: 18px;
  overflow: hidden;
  padding: 5px 5px;
}

.navbar a {
  float: left;
  text-align: center;
  text-decoration: none;
  padding: 10px 16px;
  font-size: 17px;
  width: 95px;
  border-radius: 12px;
  color: #f2f2f2;
}

.navbar a:hover {
  background-color: #474747;
}

.navbar a.active {
  background-color: #474747;
  color: white;
}

.menu {
  padding: 10px 10px;
  margin: 0 10px;
  background-color: transparent;
  border: transparent;
  border-radius: 12px;
}

.menu:hover {
  background-color: #474747;
}

@media screen and (max-width:768px) {
  .navbar p {
    display: none;
    padding: 0px 0px;
    width: 0px;
    height: 0px;
  }

  .navbar a {
    padding: 4px 8px;
    width: 40px;
    justify-content: center;

  }
  
}
